<template>
  <div class="flex text-center">
    <v-app-bar
     app color="light"
     elevation="0"
      >
    
      <v-app-bar-nav-icon
      color="black"
      >
        <v-icon large @click="back"> mdi-chevron-left </v-icon>
      </v-app-bar-nav-icon>

      <v-spacer>
        <v-toolbar-title class="flex text-center">
          <h4>
            {{ kuyu_adi }} 
          </h4>
        </v-toolbar-title>
       </v-spacer>


    </v-app-bar>

    <v-simple-table>
      <template v-slot:default>
        <thead></thead>
        <tbody>
          <tr>
            <th class="text-left">Hizmet No</th>
            <th class="text-left">
              {{ hizmet_no }}
            </th>
          </tr>
          <tr>
            <th class="text-left">Vesile</th>
            <th class="text-left">
              {{ vesile }}
            </th>
          </tr>

          <tr>
            <th class="text-left">Kuyu Sahibi</th>
            <th class="text-left">
              {{ kuyu_sahibi }}
            </th>
          </tr>

          <tr>
            <th class="text-left">Telefon</th>
            <th class="text-left">
              {{ tel }}
            </th>
          </tr>

          <tr>
            <th class="text-left">Kuyu Yeri</th>
            <th class="text-left">
              {{ kuyu_yeri }}
            </th>
          </tr>

          <tr>
            <th class="text-left">Kayıt Tarihi</th>
            <th class="text-left">
                {{kayit_tarihi.slice(0,10) }}
          
            </th>
          </tr>

          <tr>
            <th class="text-left">Grubu</th>
            <th class="text-left">
              {{ grubu }}
            </th>
          </tr>
        </tbody>
      </template>
    </v-simple-table>

 
    <!--
   Stepper Alanı
 -->
    <v-stepper
    elevation="0"
     v-model="e6" vertical>
      <v-stepper-step :complete="e6 > 1"
         step="1"
         > 
        Kayıt Oluşturuldu
        <small>
          <!--
            {{ kayit_tarihi }} Tarihinde Kayıt Edildi
          -->
        </small>
      </v-stepper-step>

      <v-stepper-content step="1">
        
        <v-card color="white" class="flex text-left" height="auto"></v-card>
      </v-stepper-content>

      <v-stepper-step :complete="e6 > 2" step="2">
       Gruplandırıldı 
      </v-stepper-step>
 
      <v-stepper-content step="2">
        <v-card color="white" class="flex text-left" height="auto"></v-card>
      </v-stepper-content>

      <v-stepper-step :complete="e6 > 3" step="3">
       Bölgeye Gönderildi
      </v-stepper-step>

      <v-stepper-content step="3">
        <v-card color="white" class="flex text-left" height="auto"></v-card>
      </v-stepper-content>



      <v-stepper-step :complete="e6 > 4" step="4">
        Çalışmaya Başlandı
      </v-stepper-step>

      <v-stepper-content step="4">
       <v-card color="white" class="flex text-left" height="auto"></v-card>
      </v-stepper-content>



      <v-stepper-step :complete="e6 > 5" step="5">
        Proje Bitti
      </v-stepper-step>

      <v-stepper-content step="5">
       <v-card color="white" class="flex text-left" height="auto"></v-card>
      </v-stepper-content>




      <v-stepper-step :complete="e6 > 6" step="6">
        Görseller Yüklendi
      </v-stepper-step>

      <v-stepper-content step="6">
       <v-card color="white" class="flex text-left" height="auto"></v-card>
      </v-stepper-content>




      <v-stepper-step step="7"> Açılış Yapıldı </v-stepper-step>
      <v-stepper-content step="7">
      <v-card color="white" class="flex text-left" height="auto"></v-card>
      </v-stepper-content>
    </v-stepper>
    <!--
   Stepper Alanı
 -->


  </div>
</template>

<script>
export default {

  
  data() {
    

    if (this.tamamlandi == "Kayıt Oluşturuldu") {
      return {
        e6: 1,
        
      };
    } else if (this.tamamlandi == "Gruplandırıldı") {
      return {
        e6: 2,

      };
    } else if (this.tamamlandi == "Bölgeye Gönderildi") {
      return {
        e6: 3,


      };
    } else if (this.tamamlandi == "Çalışmaya Başlandı") {
      return {
        e6: 4,


      };
    } 
    else if (this.tamamlandi == "Proje Bitti") {
      return {
        e6: 5,

      };
    } 
       else if (this.tamamlandi == "Görseller Yüklendi") {
      return {
        e6: 6,

      };
    }
    
        else if (this.tamamlandi == "Açılış Yapıldı") {
      return { 
        e6: 7,

      };
    }

    else {
      return {
        e6: 1,

      };
    }



  },
props: [
       "hizmet_no",
    "kuyu_adi",
    "vesile",
    "kuyu_sahibi",
    "tel",
    "kaydeden_kullanici",
    //  'aciklama',
    //  'kuyu_nevi',
    "kuyu_yeri",
    "kayit_tarihi",
    "grubu",
    // 'baslama_tarihi',
    //  'acilis',
    "tamamlandi",
 
  ],
  methods: {
    back() {
     this.$router.go(-1); 
    }, 
  
  },
 
  
};


</script>



<style>

.v-application--is-ltr .v-stepper--vertical .v-stepper__step__step{
  background: var(--first-color);
}

.theme--light.v-stepper .v-stepper__step:not(.v-stepper__step--active):not(.v-stepper__step--complete):not(.v-stepper__step--error) .v-stepper__step__step
{
  background-color: rgba(0, 0, 0, 0.38) !important;;
}
</style> 

